<script>
import ProgressBar from "@/components/common/ProgressBar";
import dayjs from "@/helpers/dayjs";
import { getTicketCount } from "@/services/pad";
import {
  getTokenDecimal,
  getTokenSymbol,
  getBlockNumber,
} from "@/services/common";
import { getLzShareInfo } from "@/services/pad";
import {
  getUpcomingIdos,
  getWhitelistedIdo,
  blockNumberToTimestamp,
  getTotalLzLock,
} from "@/helpers/utils";
import BigNumber from "@/helpers/bignumber.js";
import { mapState, mapMutations } from "vuex";
import customParseFormat from "dayjs/plugin/customParseFormat";

dayjs.extend(customParseFormat);

export default {
  name: "Home",
  components: {
    ProgressBar,
  },
  data() {
    return {
      idos: [],
      ticketCount: 0,
      showBuyShareModal: false,
      showMyShareModal: false,
      loading: true,
      totalLocked: 0,
      upcomingIdos: [],
      lzLocked: 0,
    };
  },
  computed: {
    ...mapState("wallet", ["account", "chainId"]),
    ...mapState("setting", ["theme"]),
  },
  watch: {
    account: function () {
      this.getShareInfo();
    },
  },
  methods: {
    ...mapMutations("wallet", ["TOGGLE_WALLET_MODAL"]),
    async getIdoV1(ido) {
      const { idoToken, rounds, startBlock, endBlock } = ido;

      let idoState, idoTimer, progress;

      const idoTokenSymbol = await getTokenSymbol(
        ido.metadata.chainId,
        idoToken
      );

      const idoTokenDecimal = await getTokenDecimal(
        ido.metadata.chainId,
        idoToken
      );

      const totalIDOAmt = rounds.reduce(
        (accumulator, round) => BigNumber(accumulator).plus(round.idoAmt),
        0
      );

      const totalDistributed = rounds.reduce(
        (accumulator, round) =>
          BigNumber(accumulator).plus(round.amtDistributed),
        0
      );

      progress = totalDistributed.div(totalIDOAmt).toFixed(4);
      if (progress >= 1) progress = 1;

      const currentBlock = await getBlockNumber(ido.metadata.chainId);
      const currentTime = Date.now() / 1000;

      if (currentBlock < Number(startBlock)) {
        idoState = "Start In (Approx)";
        const timestamp = blockNumberToTimestamp(
          ido.metadata.chainId,
          currentTime,
          currentBlock,
          startBlock
        );
        idoTimer = dayjs.unix(timestamp).format("MMM DD YYYY HH:mm");
      }

      if (currentBlock >= Number(startBlock)) {
        idoState = "In Progress, ended in";

        const timestamp = blockNumberToTimestamp(
          ido.metadata.chainId,
          currentTime,
          currentBlock,
          endBlock
        );
        idoTimer = dayjs.unix(timestamp).format("MMM DD YYYY HH:mm");
      }

      if (currentBlock > Number(endBlock)) {
        idoState = "Ended";
        idoTimer = "";
      }

      return {
        ...ido,
        idoState,
        idoTimer,
        progress,
        idoTokenDecimal,
        idoTokenSymbol,
        totalIDOAmt,
        totalDistributed,
      };
    },
    async getIdoV2(ido) {
      console.log("getIdoV2")
      const { idoToken, rounds, startTime, endTime } = ido;

      let idoState, idoTimer, progress;

      const idoTokenSymbol = await getTokenSymbol(
        ido.metadata.chainId,
        idoToken
      );

      const idoTokenDecimal = await getTokenDecimal(
        ido.metadata.chainId,
        idoToken
      );

      const totalIDOAmt = rounds.reduce(
        (accumulator, round) => BigNumber(accumulator).plus(round.idoAmt),
        0
      );

      const totalDistributed = rounds.reduce(
        (accumulator, round) =>
          BigNumber(accumulator).plus(round.amtDistributed),
        0
      );

      progress = totalDistributed.div(totalIDOAmt).toFixed(4);
      if (progress >= 1) progress = 1;

      const currentTime = Date.now() / 1000;

      if (currentTime < Number(startTime)) {
        idoState = "Start In";
        idoTimer = dayjs.unix(startTime).format("MMM DD YYYY HH:mm");
      }

      if (currentTime >= Number(startTime)) {
        idoState = "In Progress, ended in";
        idoTimer = dayjs.unix(endTime).format("MMM DD YYYY HH:mm");
      }

      if (currentTime > Number(endTime)) {
        idoState = "Ended";
        idoTimer = "";
      }

      return {
        ...ido,
        idoState,
        idoTimer,
        progress,
        idoTokenDecimal,
        idoTokenSymbol,
        totalIDOAmt,
        totalDistributed,
      };
    },
    async loadPools() {
      this.totalLocked = await getTotalLzLock();

      const idos = await getWhitelistedIdo();
      this.ticketCount = await getTicketCount();

      let task1 = Promise.all(idos.map(async (ido) => {
        if(!ido.metadata.version) return this.getIdoV1(ido);
        if(ido.metadata.version == "2") return this.getIdoV2(ido);
      }));

      let task2 = getUpcomingIdos();

      this.idos = await task1;
      this.upcomingIdos = await task2;
    },
    async getShareInfo() {
      const { account } = this;
      if (!account) return;

      const { totalLockV1, totalLockV2, totalLockV3 } = await getLzShareInfo(account);

      this.lzLocked = BigNumber(totalLockV1).plus(totalLockV2).plus(totalLockV3);
    },
  },
  created: async function () {
    this.getShareInfo();
    await this.loadPools();
    this.loading = false;

    this.loadPoolsInterval = setInterval(() => {
      this.loadPools();
    }, 300000);
  },
  beforeDestroy() {
    if (this.loadPoolsInterval) clearInterval(this.loadPoolsInterval);
  },
};
</script>

