<template>
  <progress-bar
    :type="type"
    ref="line"
    :options="options"
    style="width: 100%; border-radius: 20px; overflow: hidden"
  />
</template>
<script>
export default {
  props: {
    type: null,
    progress: null,
  },
  data() {
    return {
      options: {
        color: "#5aa62b",
        strokeWidth: 2,
        trailWidth: 2,
        trailColor: "gray",
      },
    };
  },
  watch: {
    progress: function (newVal) {
      this.$refs.line.animate(newVal);
    },
  },
  mounted(){
    this.$refs.line.animate(this.progress);
  }
};
</script>