<script>
import config from "@/config";
import ProgressBar from "@/components/common/ProgressBar";
import DepositRoundModal from "@/components/launchpad/v2/DepositRoundModal";
import ClaimRoundModal from "@/components/launchpad/v2/ClaimRoundModal";
import dayjs from "@/helpers/dayjs";
import {
  getTokenDecimal,
  getTokenSymbol,
  getTotalSupply,
} from "@/services/common";
import { isRoundDeposited, getIDOv2Progress } from "@/services/pad";
import BigNumber from "@/helpers/bignumber.js";
import { mapState, mapActions, mapMutations } from "vuex";
import {
  shortenAddress,
  getIdoByName,
} from "@/helpers/utils";

export default {
  components: {
    ProgressBar,
    DepositRoundModal,
    ClaimRoundModal,
  },
  data() {
    return {
      idoAddress: "",
      tokenAddress: "",
      multiplier: config.multiplier,
      accessType: "Ticket Holder + MemberShip + Whitelist",
      idoTokenSymbol: "",
      idoTokenTotalSupply: 0,
      idoTokenDecimal: 18,
      idoTokenIcon: config.customIcon,

      metadata: {},
      loading: true,
      idoState: "",
      idoTimer: "",
      rounds: [],
      roundsDeposited: [],
      roundsClaimed: [],
      activeRoundIndex: 0,

      showDepositRoundModal: false,
      showClaimRoundModal: false,
      config,
    };
  },
  computed: {
    ...mapState("wallet", ["account", "chainId"]),
    ...mapState("setting", ["theme"]),
    shortenTokenAddress() {
      return shortenAddress(this.idoToken);
    },
    totalIDOAmt() {
      return this.rounds
        .reduce(
          (accumulator, round, currentIndex) =>
            BigNumber(accumulator).plus(
              this.metadata.rounds[currentIndex].roundAllocation
                ? this.metadata.rounds[currentIndex].roundAllocation
                : round.idoAmt
            ),
          0
        )
        .toFixed(4);
    },
    correctNetwork() {
      return this.$route.params.poolChainId == this.chainId;
    },
  },
  watch: {
    account: function () {
      this.getAccountPoolInfo();
    },
  },
  methods: {
    ...mapActions("wallet", ["sendTransaction"]),
    ...mapMutations("wallet", ["TOGGLE_WALLET_MODAL", "TOGGLE_NETWORK_MODAL"]),
    async getAccountPoolInfo() {
      const { account, idoAddress, rounds } = this;
      const { poolChainId } = this.$route.params;
      if (!account) return;

      this.roundsDeposited = await Promise.all(
        rounds.map((round, index) => {
          return isRoundDeposited(poolChainId, idoAddress, index, account);
        })
      );
    },
    async getIdoInfo() {
      const { poolChainId, idoName } = this.$route.params;

      let ido = await getIdoByName(poolChainId, idoName);

      const {
        idoAddress,
        idoToken,
        multiplier,
        rounds,
        metadata,
        startTime,
        endTime,
      } = ido;

      this.idoAddress = idoAddress;
      this.idoToken = idoToken;

      const idoTokenSymbol = await getTokenSymbol(poolChainId, idoToken);
      const idoTokenDecimal = await getTokenDecimal(poolChainId, idoToken);
      const idoTokenTotalSupply = await getTotalSupply(poolChainId, idoToken);
      const currentTime = dayjs().unix();

      let idoState, idoTimer;
      if (currentTime < Number(startTime)) {
        idoState = `${Number(startTime) - Number(endTime)} seconds to go`;

        idoTimer = `at ${dayjs
          .unix(startTime)
          .utc()
          .format("HH:mm MMM DD YYYY UTC")}`;
      }

      if (currentTime >= Number(startTime)) {
        idoState = `Ends in ${Number(endTime) - Number(currentTime)} seconds`;

        idoTimer = `approx at ${dayjs
          .unix(endTime)
          .utc()
          .format("HH:mm MMM DD YYYY UTC")}`;
      }

      if (currentTime > Number(endTime)) {
        idoState = "Ended";
        idoTimer = "";
      }

      this.idoState = idoState;
      this.idoTimer = idoTimer;

      this.rounds = rounds.map((round, index) => {
        let state, timer;

        let progress = BigNumber(round.amtDistributed)
          .div(round.idoAmt)
          .toFixed(4);

        if (progress >= 1) progress = 1;

        console.log(round)

        if (currentTime < Number(round.startTime)) {
          state = "Starts in ";
          timer = `${Number(round.startTime) - Number(currentTime)} seconds`;
        }

        if (currentTime >= Number(round.startTime)) {
          state = "Ends in ";
          timer = `${Number(round.endTime) - Number(currentTime)} seconds`;
        }

        if (currentTime > Number(round.endTime)) {
          state = "Ended";
          timer = "";
        }

        return {
          ...round,
          state,
          timer,
          progress,
          metadata: metadata.rounds[index],
        };
      });

      this.metadata = metadata;
      this.idoAddress = idoAddress;
      this.idoTokenSymbol = idoTokenSymbol;
      this.idoTokenIcon = metadata.logo;
      this.idoTokenDecimal = idoTokenDecimal;
      this.idoTokenTotalSupply = idoTokenTotalSupply;
      this.multiplier = multiplier;

      this.updateProgressInterval = setInterval(async () => {
        await this.updateProgress();
      }, 20000);

      this.updateTimer = setInterval(async () => {
        const currentTime = dayjs().unix();

        if (currentTime < Number(startTime)) {
          idoState = `${
            Number(startTime) - Number(currentTime)
          } seconds to go`;
    
          idoTimer = `at ${dayjs
            .unix(startTime)
            .utc()
            .format("HH:mm MMM DD YYYY UTC")}`;
        }

        if (currentTime >= Number(startTime)) {
          idoState = `Ends in ${
            Number(endTime) - Number(currentTime)
          } seconds`;

          idoTimer = `at ${dayjs
            .unix(endTime)
            .utc()
            .format("HH:mm MMM DD YYYY UTC")}`;
        }

        if (currentTime > Number(endTime)) {
          idoState = "Ended";
          idoTimer = "";
        }

        this.idoState = idoState;
        this.idoTimer = idoTimer;

        this.rounds = this.rounds.map((round) => {
          let state, timer;

          if (currentTime < Number(round.startTime)) {
            state = "Starts in ";
            timer = `${Number(round.startTime) - Number(currentTime)} seconds`;
          }

          if (currentTime >= Number(round.startTime)) {
            state = "Ends in ";
            timer = `${Number(round.endTime) - Number(currentTime)} seconds`;
          }

          if (currentTime > Number(round.endTime)) {
            state = "Ended";
            timer = "";
          }

          return { ...round, state, timer };
        });
      }, 1000);
    },
    async updateProgress() {
      const { idoAddress } = this;
      const { poolChainId } = this.$route.params;

      const { rounds } = await getIDOv2Progress(poolChainId, idoAddress);
      const currentTime = dayjs().unix();

      this.rounds = rounds.map((round, index) => {
        let progress = BigNumber(round.amtDistributed)
          .div(round.idoAmt)
          .toFixed(4);

        if (progress >= 1) progress = 1;

        let state, timer;

        if (currentTime < Number(round.startTime)) {
          state = "Starts in ";
          timer = `${Number(round.startTime) - Number(currentTime)} seconds`;
        }

        if (currentTime >= Number(round.startTime)) {
          state = "Ends in ";
          timer = `${Number(round.endTime) - Number(currentTime)} seconds`;
        }

        if (currentTime > Number(round.endTime)) {
          state = "Ended";
          timer = "";
        }

        return {
          ...round,
          state,
          timer,
          progress,
          metadata: this.metadata.rounds[index],
        };
      });
    },
    async showRoundAction(index) {
      this.activeRoundIndex = index;

      if (!this.roundsDeposited[index]) this.showDepositRoundModal = true;
      else this.showClaimRoundModal = true;
    },
    onCopy: function (e) {
      console.log(e);
      this.$notify({
        group: "copy",
        title: "Address copied",
        text: this.shortenTokenAddress,
      });
    },
    onError: function () {
      this.$notify({ group: "copy", text: "Failed to copy texts" });
    },
  },
  async mounted() {
    await this.getIdoInfo();
    this.loading = false;
    await this.getAccountPoolInfo();
  },
  beforeDestroy() {
    if (this.updateTimer) clearInterval(this.updateTimer);
    if (this.updateProgressInterval) clearInterval(this.updateProgressInterval);
  },
};
</script>

