<template>
  <div class="pad-page">
    <h1 class="pad-title text-linear">Pad</h1>
    <div class="pad-overview">
      <div class="text-linear pad-slogan font-light">
        Innovative, Decentralized, and Outstanding
      </div>
      <div class="pad-info">
        <div class="top">
          <div class="left">
            <div class="text-green">Total LZ Locked:</div>
            <div class="text-linear total-locked">
              {{
                $filters.currency($filters.normalizeBalance(totalLocked, 18))
              }}
            </div>
          </div>
          <div class="right">
            <img src="@/assets/media/moneyhand.png" />
          </div>
        </div>
        <div class="bot">
          <div class="left">
            <div>Your LZ locked:</div>
            <div>
              {{ $filters.currency($filters.normalizeBalance(lzLockedV3, 18)) }}
            </div>
          </div>
          <div class="right">
            <div v-if="account" class="btn-detail">Portfolio</div>
            <button
              v-else
              class="btn-detail"
              @click="TOGGLE_WALLET_MODAL(true)"
            >
              Connect
            </button>
          </div>
        </div>
