    <div class="portfolio">
      <div class="lock-lz-card">
        <div class="title text-linear">Lock more LZ</div>
        <div class="token-balance">
          <span>Balance:</span>
          <span>
            {{ $filters.currency($filters.normalizeBalance(lzBalance, 18)) }} LZ
          </span>
        </div>
        <div class="token-balance">
          <span>Your LZ locked:</span>
          <span>
            {{ $filters.currency($filters.normalizeBalance(lzLockedV3, 18)) }} LZ
          </span>
        </div>
        <div class="field-token-input">
          <img
            style="border-radius: 50%"
            alt=""
            src="@/assets/media/lz-token-icon.svg"
          />
          <input
            placeholder="0"
            type="number"
            autocomplete="off"
            v-model="inputAmount"
          />
          <button @click="setMax">Max</button>
          <span>LZ</span>
        </div>

        <div class="remind">
          <div>Lock LZ to have guarantee amount in our future IDO</div>
          <div>Your allocation ratio with your locked amount</div>
          <div>Lock time duration: <span class="text-green">6</span> month</div>
          <div>
            <a
              class="text-linear"
              rel="noopener noreferrer"
              href="https://docs.lz.finance/our-products/lz-pad"
              target="_blank"
            >
              More information
            </a>
          </div>
        </div>
        <div class="amm-btn-swap">
          <button
            v-if="!correctChain"
            @click="switchChain(56)"
            :disabled="switchLoading"
          >
            <div
              class="loader"
              style="width: 30px; height: 30px; margin: 0"
              v-if="switchLoading"
            ></div>
            <div v-else>Switch to BSC</div>
          </button>

          <button
            v-else-if="approved"
            :disabled="!isValidAmount || buyLoading"
            @click="buyShare"
          >
            <template v-if="!buyLoading">
              <div v-if="isValidAmount">Lock LZ</div>
              <div v-else>Insufficient Amount</div>
            </template>
            <template v-else>
              <div class="amm-btn-loading"></div>
            </template>
          </button>

          <button
            v-else-if="!approved"
            @click="approve"
            :disabled="approveLoading"
          >
            <div class="amm-btn-loading" v-if="approveLoading"></div>
            <div v-else>Approve LZ</div>
          </button>
        </div>
      </div>
      <div class="unlock-lz-card">
        <div class="title text-linear">Diamond Hand Pool</div>
        <div class="body">
          <table style="margin-top: 20px; font-size: 0.75rem">
            <tr>
              <th>Locked</th>
              <th>Next Unlock *</th>
              <th>Unlockable</th>
              <th>Action</th>
            </tr>
            <tr v-if="bscxLocked > 0">
              <td>
                {{
                  $filters.currency($filters.normalizeBalance(bscxLocked, 18))
                }}
                BSCX
              </td>
              <td>
                {{ nextUnlockBscxTime }}
              </td>
              <td>
                {{
                  $filters.currency(
                    $filters.normalizeBalance(bscxUnlockable, 18)
                  )
                }}
                BSCX
              </td>
              <td>
                <button
                  v-if="!correctChain"
                  @click="switchChain(56)"
                  :disabled="switchLoading"
                  class="claim-btn"
                >
                  <div class="loader" v-if="switchLoading"></div>
                  <div v-else>Switch to BSC</div>
                </button>
                <button
                  v-else
                  @click="unlockBscx"
                  :disabled="unlockBscxLoading || bscxUnlockable <= 0"
                  class="claim-btn"
                >
                  <div v-if="unlockBscxLoading" class="loader"></div>
                  <div v-else>Unlock</div>
                </button>
              </td>
            </tr>
            <tr v-if="lzLockedV1 > 0">
              <td>
                {{
                  $filters.currency($filters.normalizeBalance(lzLockedV1, 18))
                }}
                LZ
              </td>
              <td>
                {{ nextUnlockLzTimeV1 }}
              </td>
              <td>
                {{
                  $filters.currency(
                    $filters.normalizeBalance(lzUnlockableV1, 18)
                  )
                }}
                LZ
              </td>
              <td>
                <button
                  v-if="!correctChain"
                  @click="switchChain(56)"
                  :disabled="switchLoading"
                  class="claim-btn"
                >
                  <div class="loader" v-if="switchLoading"></div>
                  <div v-else>Switch to BSC</div>
                </button>
                <button
                  v-else
                  @click="unlockLZv1"
                  :disabled="unlockLzv1Loading || lzUnlockableV1 <= 0"
                  class="claim-btn"
                >
                  <div v-if="unlockLzv1Loading" class="loader"></div>
                  <div v-else>Unlock</div>
                </button>
              </td>
            </tr>
            <tr v-if="lzLockedV2 > 0">
              <td>
                {{
                  $filters.currency($filters.normalizeBalance(lzLockedV2, 18))
                }}
                LZ
              </td>
              <td>
                {{ nextUnlockLzTimeV2 }}
              </td>
              <td>
                {{
                  $filters.currency(
                    $filters.normalizeBalance(lzUnlockableV2, 18)
                  )
                }}
                LZ
              </td>
              <td>
                <button
                  v-if="!correctChain"
                  @click="switchChain(56)"
                  :disabled="switchLoading"
                  class="claim-btn"
                >
                  <div class="loader" v-if="switchLoading"></div>
                  <div v-else>Switch to BSC</div>
                </button>
                <button
                  v-else
                  @click="unlockLZv2"
                  :disabled="unlockLzv2Loading || lzUnlockableV2 <= 0"
                  class="claim-btn"
                >
                  <div v-if="unlockLzv2Loading" class="loader"></div>
                  <div v-else>Unlock</div>
                </button>
              </td>
            </tr>
            <tr v-if="lzLockedV3 > 0">
              <td>
                {{
                  $filters.currency($filters.normalizeBalance(lzLockedV3, 18))
                }}
                LZ
              </td>
              <td>
                {{ nextUnlockLzTimeV3 }}
              </td>
              <td>
                {{
                  $filters.currency(
                    $filters.normalizeBalance(lzUnlockableV3, 18)
                  )
                }}
                LZ
              </td>
              <td>
                <button
                  v-if="!correctChain"
                  @click="switchChain(56)"
                  :disabled="switchLoading"
                  class="claim-btn"
                >
                  <div class="loader" v-if="switchLoading"></div>
                  <div v-else>Switch to BSC</div>
                </button>
                <button
                  v-else
                  @click="unlockLZv3"
                  :disabled="unlockLzv3Loading || lzUnlockableV3 <= 0"
                  class="claim-btn"
                >
                  <div v-if="unlockLzv3Loading" class="loader"></div>
                  <div v-else>Unlock</div>
                </button>
              </td>
            </tr>
          </table>
          <div class="note">
            <div>* Note:</div>
            <div>
              - Each wallet can lock LZ in maximum 20 batches, the amount of LZ
              to be locked in each batch is unlimited.
            </div>
            <div>
              - Your locked LZ will be automatically unlocked exactly after 6
              months from the lock date.
            </div>
            <div>
              - In case you lock your LZ in different batches, each batch will
              be unlocked at a different date.
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getTotalLzLock } from "@/helpers/utils";
import { mapState, mapMutations, mapActions } from "vuex";
import config from "@/config";
import BigNumber from "@/helpers/bignumber.js";
import {
  getGasPrice,
  approveEncodeABI,
  getAllowance,
  getBalance,
} from "@/services/common";
import {
  getBscxTickets,
  buyShareEncodeABI,
  getLzShareInfo,
  unlockAllBscxTicketEncodeABI,
  unlockLZEncodeABI,
} from "@/services/pad";
import { debounce } from "@/helpers/utils";
import dayjs from "@/helpers/dayjs";

export default {
  name: "Home",
  components: {},
  data() {
    return {
      idos: [],
      ticketCount: 0,
      showBuyShareModal: false,
      showMyShareModal: false,
      loading: true,
      totalLocked: 0,
      upcomingIdos: [],
      lzLockedV1: 0,
      lzLockedV2: 0,
      lzLockedV3: 0,
      inputAmount: 0,
      buyLoading: false,
      approved: false,
      approveLoading: false,
      switchLoading: false,
      lzBalance: 0,

      bscxLocked: 0,
      nextUnlockBscxTime: null,
      nextUnlockLzTimeV1: null,
      bscxUnlockable: 0,
      lzUnlockableV1: 0,
      unlockBscxLoading: false,
      unlockLzv1Loading: false,
      unlockLzv3Loading: false,
    };
  },
  computed: {
    ...mapState("wallet", ["account", "chainId"]),
    ...mapState("setting", ["theme"]),
    isValidAmount() {
      return Number(this.inputAmount) > 0;
    },
    correctChain() {
      return this.chainId == 56;
    },
  },
  watch: {
    account: function () {
      this.getShareInfo();
      this.getLZBalance();
    },
    inputAmount: debounce(function () {
      this.calculateAllowance();
    }, 500),
  },
  methods: {
    ...mapMutations("wallet", ["TOGGLE_WALLET_MODAL"]),
    ...mapActions("wallet", ["switchChain", "sendTransaction"]),
    async loadPools() {
      this.totalLocked = await getTotalLzLock();
    },
    async getShareInfo() {
      const { account } = this;
      if (!account) return;

      const {
        totalLockV1,
        unlockableV1,
        nextUnlockableTimeV1,
        totalLockV2,
        unlockableV2,
        nextUnlockableTimeV2,
        totalLockV3,
        unlockableV3,
        nextUnlockableTimeV3,
      } = await getLzShareInfo(account);

      this.lzLockedV1 = totalLockV1;
      this.lzLockedV2 = totalLockV2;
      this.lzLockedV3 = totalLockV3;

      const bscxTickets = await getBscxTickets(account);

      let bscxLocked = BigNumber(0);
      let bscxUnlockable = BigNumber(0);
      let nextUnlockBscxTime = Number.MAX_VALUE;
      const currentUnixTime = Date.now() / 1000;

      bscxTickets.forEach((ticket) => {
        bscxLocked = bscxLocked.plus(ticket.bscxLocked);
        if (Number(ticket.lockTime) <= currentUnixTime)
          bscxUnlockable = bscxUnlockable.plus(ticket.bscxLocked);
        if (
          Number(ticket.lockTime) < nextUnlockBscxTime &&
          Number(ticket.lockTime) > currentUnixTime
        )
          nextUnlockBscxTime = Number(ticket.lockTime);
      });

      this.nextUnlockBscxTime = dayjs.unix(Number(nextUnlockBscxTime)).isValid()
        ? dayjs.unix(Number(nextUnlockBscxTime)).format("MMM DD YYYY HH:mm")
        : "Ended";
      this.bscxUnlockable = bscxUnlockable;
      this.bscxLocked = bscxLocked;

      this.lzUnlockableV1 = unlockableV1;
      this.nextUnlockLzTimeV1 = dayjs
        .unix(Number(nextUnlockableTimeV1))
        .isValid()
        ? dayjs.unix(Number(nextUnlockableTimeV1)).format("MMM DD YYYY HH:mm")
        : "Ended";

      this.lzUnlockableV2 = unlockableV2;
      this.nextUnlockLzTimeV2 = dayjs
        .unix(Number(nextUnlockableTimeV2))
        .isValid()
        ? dayjs.unix(Number(nextUnlockableTimeV2)).format("MMM DD YYYY HH:mm")
        : "Ended";

      this.lzUnlockableV3 = unlockableV3;
      this.nextUnlockLzTimeV3 = dayjs
        .unix(Number(nextUnlockableTimeV3))
        .isValid()
        ? dayjs.unix(Number(nextUnlockableTimeV3)).format("MMM DD YYYY HH:mm")
        : "Ended";
    },
    async getLZBalance() {
      this.lzBalance = await getBalance(
        56,
        config.chain[56].addresses.lz,
        this.account
      );
    },
    async unlockBscx() {
      try {
        this.unlockBscxLoading = true;
        const data = await unlockAllBscxTicketEncodeABI();
        const res = await this.sendTransaction({
          value: 0,
          data,
          to: config.chain[56].addresses.bscxTicket,
        });

        this.getShareInfo();

        this.$notify({
          group: "success",
          title: `Unlock BSCX succeeded`,
          text: `<a href="${config.chain[56].explorer}/tx/${res.transactionHash}" target="_blank">View on Explorer</>`,
        });
      } catch (e) {
        console.log(e);
      } finally {
        this.unlockBscxLoading = false;
      }
    },
    async unlockLZv1() {
      this.$notify({
        group: "error",
        title: `Maintainance`,
        text: ``,
      });

      // try {
      //   this.unlockLzv1Loading = true;
      //   const data = await unlockLZEncodeABI();
      //   const res = await this.sendTransaction({
      //     value: 0,
      //     data,
      //     to: config.chain[56].addresses.launchpadSharePool,
      //   });

      //   this.getShareInfo();

      //   this.$notify({
      //     group: "success",
      //     title: `Unlock LZ succeeded`,
      //     text: `<a href="${config.chain[56].explorer}/tx/${res.transactionHash}" target="_blank">View on Explorer</>`,
      //   });
      // } catch (e) {
      //   console.log(e);
      // } finally {
      //   this.unlockLzv1Loading = false;
      // }
    },
    async unlockLZv2() {
      try {
        this.unlockLzv2Loading = true;
        const data = await unlockLZEncodeABI();
        const res = await this.sendTransaction({
          value: 0,
          data,
          to: config.chain[56].addresses.launchpadSharePoolv2,
        });

        this.getShareInfo();

        this.$notify({
          group: "success",
          title: `Unlock LZ succeeded`,
          text: `<a href="${config.chain[56].explorer}/tx/${res.transactionHash}" target="_blank">View on Explorer</>`,
        });
      } catch (e) {
        console.log(e);
      } finally {
        this.unlockLzv2Loading = false;
      }
    },
    async unlockLZv3() {
      try {
        this.unlockLzv3Loading = true;
        const data = await unlockLZEncodeABI();
        const res = await this.sendTransaction({
          value: 0,
          data,
          to: config.chain[56].addresses.launchpadSharePoolv3,
        });

        this.getShareInfo();

        this.$notify({
          group: "success",
          title: `Unlock LZ succeeded`,
          text: `<a href="${config.chain[56].explorer}/tx/${res.transactionHash}" target="_blank">View on Explorer</>`,
        });
      } catch (e) {
        console.log(e);
      } finally {
        this.unlockLzv3Loading = false;
      }
    },
    calculateAllowance: async function () {
      const { account, inputAmount } = this;
      if (!account) return;
      if (!inputAmount) return (this.approved = true);
      const allowance = await getAllowance(
        56,
        config.chain[56].addresses.lz,
        account,
        config.chain[56].addresses.launchpadSharePoolv3
      );
      if (BigNumber(allowance).gte(BigNumber(inputAmount).shiftedBy(18)))
        this.approved = true;
      else this.approved = false;
    },
    setMax() {
      this.inputAmount = BigNumber(this.lzBalance).shiftedBy(-18).toString();
    },
    async approve() {
      this.approveLoading = true;

      try {
        const data = await approveEncodeABI(
          56,
          config.chain[56].addresses.lz,
          config.chain[56].addresses.launchpadSharePoolv3,
          BigNumber(10 ** 27)
        );

        const res = await this.sendTransaction({
          value: 0,
          data,
          to: config.chain[56].addresses.lz,
          gasPrice: await getGasPrice(56),
        });

        this.approved = true;

        this.$notify({
          group: "success",
          title: `Approved LZ`,
          text: `<a href="${config.chain[56].explorer}/tx/${res.transactionHash}" target="_blank">View on Explorer</>`,
        });
      } catch (error) {
        console.log(error);
        this.$notify({
          group: "error",
          title: "Transaction Error",
          text:
            error.message.length && error.message.length > 200
              ? "Transaction error, please try again later"
              : error.message,
        });
      } finally {
        this.approveLoading = false;
      }
    },
    async buyShare() {
      try {
        const { inputAmount } = this;

        this.buyLoading = true;
        const data = await buyShareEncodeABI(
          BigNumber(inputAmount).shiftedBy(18)
        );

        const res = await this.sendTransaction({
          value: 0,
          data,
          to: config.chain[56].addresses.launchpadSharePoolv3,
        });

        this.$emit("buySuccess");

        this.$notify({
          group: "success",
          title: `${inputAmount} LZ successfully deposited`,
          text: `<a href="${config.chain[56].explorer}/tx/${res.transactionHash}" target="_blank">View on Explorer</>`,
        });
      } catch (e) {
        console.log(e);
      } finally {
        this.buyLoading = false;
        this.$emit("close");
      }
    },
  },
  created: async function () {
    this.getLZBalance();
    this.getShareInfo();
    this.calculateAllowance();
    await this.loadPools();
  },
  beforeDestroy() {
    if (this.loadPoolsInterval) clearInterval(this.loadPoolsInterval);
  },
};
</script>

