<template>
  <div class="modal-mask">
    <div class="modal-wrapper">
      <div class="modal-container">
        <template v-if="!claimLoading">
          <div class="modal-header">
            <span>Claim Vested - {{ roundName }}</span>
            <span @click="$emit('close')">
              <svg
                width="20"
                height="20"
                viewBox="0 0 20 20"
                fill="none"
                stroke="currentColor"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
              >
                <line x1="18" y1="6" x2="6" y2="18"></line>
                <line x1="6" y1="6" x2="18" y2="18"></line>
              </svg>
            </span>
          </div>

          <div class="modal-body">
            <div class="token-balance">
              Balance:
              {{
                $filters.currency(
                  $filters.normalizeBalance(idoTokenBalance, idoTokenDecimal)
                )
              }}
            </div>
            <div class="field-token-output">
              <img style="border-radius: 50%" alt="" :src="idoTokenIcon" />
              <input
                placeholder="0.0"
                autocomplete="off"
                type="text"
                disabled
                :value="
                  $filters.currency(
                    $filters.normalizeBalance(claimableAmt, idoTokenDecimal)
                  )
                "
              />
              <span>{{ idoTokenSymbol }}</span>
            </div>
            <!-- <div class="remind">
              Claimable:
              {{
                $filters.currency(
                  $filters.normalizeBalance(claimableAmt, idoTokenDecimal)
                )
              }}
              {{ idoTokenSymbol }}
            </div>
            <div class="remind">
              Claimed:
              {{
                $filters.currency(
                  $filters.normalizeBalance(unlockedAmt, idoTokenDecimal)
                )
              }}
              {{ idoTokenSymbol }}
            </div> -->
            <div class="remind">
              Locked remain:
              {{
                $filters.currency(
                  $filters.normalizeBalance(lockAmt, idoTokenDecimal)
                )
              }}
              {{ idoTokenSymbol }}
            </div>
            <div class="remind">Next claim in block: {{ nexUnlockBlock }}</div>
            <div v-if="!correctChain" class="amm-btn-swap">
              <button
                @click="switchChain($route.params.poolChainId)"
                :disabled="switchLoading"
              >
                <div
                  class="loader"
                  v-if="switchLoading"
                  style="width: 30px; height: 30px; margin: 0"
                ></div>
                <div v-else>
                  Switch to
                  {{ config.chain[$route.params.poolChainId].chainName }}
                </div>
              </button>
            </div>
            <div v-else class="amm-btn-swap">
              <button @click="claim" :disabled="!isClaimable">
                <div>Claim</div>
              </button>
            </div>
          </div>
        </template>

        <template v-else>
          <div class="modal-header">
            <span></span>
            <span @click="$emit('close')">
              <svg
                width="20"
                height="20"
                viewBox="0 0 20 20"
                fill="none"
                stroke="currentColor"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
              >
                <line x1="18" y1="6" x2="6" y2="18"></line>
                <line x1="6" y1="6" x2="18" y2="18"></line>
              </svg>
            </span>
          </div>
          <div class="confirm-loading">
            <div class="loader"></div>
            <div class="confirm-text">Waiting For Confirmation</div>
            <div class="confirm-text" style="font-size: 14px; row-gap: 12px">
              Claiming
              {{
                $filters.currency(
                  $filters.normalizeBalance(claimableAmt, idoTokenDecimal)
                )
              }}
              {{ idoTokenSymbol }}
            </div>
            <div class="confirm-wallet">
              Confirm this transaction in your wallet
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>
<script>
import BigNumber from "@/helpers/bignumber.js";
import { mapState, mapActions } from "vuex";
import { getBalance } from "@/services/common";
import {
  claimEncodeABI,
  getClaimableAmt,
  getVestingInfo,
  getNextTimeClaimable,
} from "@/services/pad";
import config from "@/config";

export default {
  name: "ClaimHeroModal",
  props: {
    idoTokenSymbol: null,
    idoTokenIcon: null,
    idoTokenDecimal: null,
    idoAddress: null,
    idoToken: null,
    round: null,
    roundIndex: null,
    roundName: null,
  },
  data() {
    return {
      claimableAmt: 0,
      claimLoading: false,
      idoTokenBalance: 0,
      nexUnlockBlock: "Loading",
      unlockedAmt: 0,
      lockAmt: 0,
      isClaimable: false,
      switchLoading: false,
      config,
    };
  },
  computed: {
    ...mapState("wallet", ["account", "chainId"]),
    correctChain() {
      return this.chainId == this.$route.params.poolChainId;
    },
  },
  methods: {
    ...mapActions("wallet", ["sendTransaction", "switchChain"]),
    async claim() {
      const { poolChainId } = this.$route.params;
      const { idoAddress, chainId } = this;

      if (chainId != poolChainId) {
        this.$notify({
          group: "error",
          title: `Wrong Network`,
          text: `Please switch to ${config.chain[poolChainId].chainName}`,
        });

        return;
      }

      this.claimLoading = true;
      try {
        const data = await claimEncodeABI(
          poolChainId,
          idoAddress,
          this.roundIndex
        );
        const res = await this.sendTransaction({
          value: 0,
          data,
          to: idoAddress,
        });

        this.$emit("claimSuccess");

        this.$notify({
          group: "success",
          title: `Claim succeeded`,
          text: `<a href="${config.chain[poolChainId].explorer}/tx/${res.transactionHash}" target="_blank">View on Explorer</>`,
        });
      } catch (e) {
        console.log(e);
      } finally {
        this.claimLoading = false;
        this.$emit("close");
      }
    },
    async updateTokenBalance() {
      const { poolChainId } = this.$route.params;
      const { idoToken, account } = this;

      this.idoTokenBalance = await getBalance(poolChainId, idoToken, account);
    },
    async loadVestedInfo() {
      const { poolChainId } = this.$route.params;
      const { idoAddress, account, roundIndex } = this;
      this.claimableAmt = await getClaimableAmt(
        poolChainId,
        idoAddress,
        roundIndex,
        account
      );
      this.isClaimable = BigNumber(this.claimableAmt).gt(0);
      const { unlockedAmt, lockAmt } = await getVestingInfo(
        poolChainId,
        idoAddress,
        roundIndex,
        account
      );

      let nexUnlockBlock = await getNextTimeClaimable(
        poolChainId,
        idoAddress,
        roundIndex,
        account
      );
      if (nexUnlockBlock > 0) this.nexUnlockBlock = nexUnlockBlock;
      else this.nexUnlockBlock = "Ended";
      this.unlockedAmt = unlockedAmt;
      this.lockAmt = lockAmt;
    },
  },
  async created() {
    this.loadVestedInfo();
    this.updateTokenBalance();
    this.updateInterval = setInterval(() => {
      this.updateTokenBalance();
    }, 6000);
  },
  destroyed() {
    if (this.updateInterval) clearInterval(this.updateInterval);
  },
};
</script>
